.image {
    display: none;
}

@media screen and (min-width: 768px) {
    .image {
        margin: 156px 0 0 0px;
        display: block;
        height: 400px;
        border-radius: 3px;
        box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 1px rgb(0 0 0 / 14%),
            0 2px 1px rgb(0 0 0 / 20%);
    }

    .contantWrap {
        width: 60%;
    }
}

@media screen and (min-width: 1200px) {
    .contantWrap {
        width: 70%;
    }
}

.title {
    font-size: 50px;
    letter-spacing: 1.5px;
}

.subtitle {
    font-size: 30px;
    margin-top: 50px;
}